.home-cont-left{
	width: 40%;
	margin-left: 85px;
	margin-top: 90px;
}
.home-cont-left-text-item{
	font-weight: bold;
	letter-spacing: 1.2px;
	font-size: 40px;
	line-height: 47px;
	span{
		color: #77b231;
	}
}
.home-cont-left-text-item-two{
	letter-spacing: 0.7px;
	width: 385px;
}
.home-cont-rigth{
width: 60%;
}

.home-cont-left-form{
	width: 520px;
	height: 455px;
	margin-top: 40px;
	border-radius: 6px;
	-webkit-box-shadow: 0px 6px 10px -2px rgba(0,0,0,0.75);
	-moz-box-shadow: 0px 6px 10px -2px rgba(0,0,0,0.75);
	box-shadow: 0px 6px 10px -2px rgba(0,0,0,0.75);
		h5{
			font-size: 19px;
			padding-top: 18px;
		}
}

.home-cont-left-form-setting{
	position: relative;
	margin-left:20px;
	top: 16px;
	font-size: 15px;
	a{
		margin-left:20px;
	}
}

.home-cont-left-form-input{
	margin-left: 25px;
	width: 230px;
	input{
	width: 220px;
	height: 40px;
	border: 1px solid #ced4da;
	border-radius: 3px;
	margin-top: 15px;
	padding: .375rem .75rem;
	}
}

.home-cont-left-form-input-item{
	margin-left: 15px;
	input{
	width: 220px;
	height: 40px;
	border: 1px solid #ced4da;
	border-radius: 3px;
	margin-top: 15px;
	padding: .375rem .75rem;
	}
}
.home-cont-left-form-textarea{
	width: 460px;
	height: 80px;
	border: solid 1px;
	margin-left: 25px;
	margin-top: 10px;
	border-radius: 3px;
	padding: .375rem .75rem;
	border: 1px solid #ced4da;

}

.my-form-group{
	margin-top: 15px;
	margin-bottom: 0px;
}
.home-cont-left-form-btnall{

	a{
		line-height: 35px;
		font-size: 15px;
		margin-left: calc(50% - 68px);
	}
}
.home-cont-left-form-btn{
	border:none;
	border-radius: 3px;
	width: 460px;
	height: 45px;
	color: #fff;
	background: #9fd75d;
	margin-top: 15px;
	margin-left: 30px;
	letter-spacing: 1.1px;
	transition: 0.5s;
	&:hover{
		cursor: pointer;
		-webkit-box-shadow: 0px 6px 10px -2px rgba(0,0,0,0.75);
		-moz-box-shadow: 0px 6px 10px -2px rgba(0,0,0,0.75);
		box-shadow: 0px 6px 10px -2px rgba(0,0,0,0.75);
		transition: 0.3s;
	}
}
